@import url("https://fonts.googleapis.com/css?family=Bitter");
@import url('https://fonts.googleapis.com/css?family=Abel');

html {
}

body {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

a:focus, a:active, a:hover {
  text-decoration:none !important;
}

.Main {
  text-align: center;
}

/* HEADER */

.Header {
  background-color: #212121;
  line-height: 26px;
  font-family: "Josefin Sans", sans-serif;
  font-size: 11px;
}

.Header a {
  color: lightgrey;
  -webkit-transition: background-color 300ms linear;
  -o-transition: background-color 300ms linear;
  transition: background-color 300ms linear;
  padding-left: 10px;
  padding-right: 10px;
  display: inline-block;
}

.active {
  -webkit-transition: background-color 300ms linear;
  -o-transition: background-color 300ms linear;
  transition: background-color 300ms linear;
  background-color: white !important;
  color: #212121 !important;
}

.section {
  text-align: center;
  font-size: 3em;
  height: 100vh;
}

/* INTRO */

#intro {
  display: none;
}

#introDivider {
  width: 250px;
  background-color: lightgrey;
  height: 2px;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 14px;
}

#intro h3 {
  font-family: "Bitter";
  color: #989898;
}

/* PICTURE FLIP ANIMATION */

.container {
  width: 200px;
  height: 200px;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  margin-top: 100px;
  padding: 0 !important;
  margin-bottom: 150px !important;
}

#profilePic,
#fakePic {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 1px solid white;
  margin-top:-50px;
}

#profilePic {
  -webkit-filter: grayscale(70%);
  filter: grayscale(70%);
}

#fakePic {
  -webkit-filter: grayscale(20%);
  filter: grayscale(20%);
}

#card {
  width: 100%;
  height: 100%;
  position: absolute;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  -o-transition: transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}

#card:hover {
  cursor: pointer;
}

#card figure {
  margin: 0;
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

#card .back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

#card.flipped {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.previousButton svg polygon,
.nextButton svg polygon {
  fill: black !important;
}

.previousButton svg,
.nextButton svg {
  width: 15px !important;
  height: 15px !important;
  background-color: white;
  border-radius: 50%;
  border: 1px solid white;
  padding: 1px;
  z-index:1000;
}

.slider {
  width: 100% !important;
  height: 100vh !important;
  z-index: 1 !important;
}

.slider img {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translateY(-50%) translateX(-50%);
}

.slider img:hover {
  cursor: pointer;
}

.description {
  z-index: 99 !important;
  color: lightgrey;
  position: absolute;
  text-align: center;
  width: 100%;
  bottom: 0;
  padding: 10px;
  font-size: 13px;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 2px;
  padding-bottom: 20px;
}

.tooltip {
  background-color:#212121 !important;
  border:1px solid #242424;
  opacity:1 !important;
  font-size: 13px !important;
  font-family: 'Abel', sans-serif;
  max-width:250px !important;
}

.social img {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: transform 100ms linear;
  -webkit-transition: -webkit-transform 100ms linear;
  transition: -webkit-transform 100ms linear;
  -o-transition: transform 100ms linear;
  transition: transform 100ms linear;
  transition: transform 100ms linear, -webkit-transform 100ms linear;
  margin-right: 10px;
  margin-left: 10px;
  border-radius: 50%;
}

.social img:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-transition: transform 100ms linear;
  -webkit-transition: -webkit-transform 100ms linear;
  transition: -webkit-transform 100ms linear;
  -o-transition: transform 100ms linear;
  transition: transform 100ms linear;
  transition: transform 100ms linear, -webkit-transform 100ms linear;
}

#home {
  padding-left: 5px;
  padding-right: 5px;
}

.indicator {
  width:100%;
  color:white;
  position:absolute;
  bottom:50px;
  font-size:14px;
  text-align:center;
}

.indicatorChild {
  display:block;
  margin:0 auto;
  width:40px;
  background-color:rgba(0,0,0,0.6);
  border-radius:2px;
  padding-bottom:3px;
  padding-top:3px;
}

label {
  display:block;
  font-size:12px;
}

.d3holder {
  margin-top:20px;
}

.d3div {
  display:inline-block;
  margin-left:20px;
  margin-right:20px;
  margin-bottom:5px;
  -webkit-box-shadow:3px -2px steelblue;
          box-shadow:3px -2px steelblue;
  z-index:1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: transform 100ms linear;
  -webkit-transition: -webkit-transform 100ms linear;
  transition: -webkit-transform 100ms linear;
  -o-transition: transform 100ms linear;
  transition: transform 100ms linear;
  transition: transform 100ms linear, -webkit-transform 100ms linear;
}

.d3div img {
  width:190px;
  border:1px solid lightgrey;
}

.d3label {
  height:16px;
  margin-top:-16px;
  position:absolute;
  font-size:11px;
  text-align:center;
  width:190px;
  background-color:rgba(0,0,0,0.9);
}

.d3label {
  color:lightgrey;
  font-family: "Josefin Sans", sans-serif;
  line-height:20px;
}

.d3label_label {
  padding:0;
  margin:0;
  position:absolute;
  left:0;
  margin-top:-20px;
  width:20px;
  height:16px;
  background-color:#A42C3D;
}

.d3div:hover {
  position:relative;
  -webkit-transform: scale(1.5);
      -ms-transform: scale(1.5);
          transform: scale(1.5);
  z-index:2;
  -webkit-transition: transform 100ms linear;
  -webkit-transition: -webkit-transform 100ms linear;
  transition: -webkit-transform 100ms linear;
  -o-transition: transform 100ms linear;
  transition: transform 100ms linear;
  transition: transform 100ms linear, -webkit-transform 100ms linear;
}

.Header-mobile {
  display:none;
}

.Header-mobile-dropdown {
  display:none;
}

@media screen and (max-width:600px) {
  .tooltip {
    display:none !important;
  }
  .Header {
    display:none;
  }
  .Header-mobile {
    display:block;
    height:22px;
    background-color:#212121;
  }
  .Header-mobile button {
    border:none;
    background-color:#212121;
    color:lightgrey;
    margin-left:15px;
  }

  .Header-mobile button:focus, active {
    outline:none;
    color:white;
  }

  .Header-mobile-dropdown {
    margin-top:10px;
    margin-left:10px;
  }

  .Header-mobile-dropdown a {
    display:block;
    width:150px;
    padding:5px;
    padding-left:10px;
    margin-bottom:3px;
    background-color:rgba(0,0,0,0.6);
    color:white;
    font-size:10px;
  }

}




